body {
  color: #333;
  text-align: center;
  background-color: #f0f0f0;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  margin: 0;
  font-family: sans-serif;
  display: flex;
}

.container {
  background-color: #fff;
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0 0 10px #0000001a;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  margin-top: 10px;
  padding: 10px 15px;
  font-size: 16px;
}

button:hover {
  background-color: #0056b3;
}

input[type="number"] {
  border: 1px solid #ccc;
  border-radius: 5px;
  width: calc(100% - 18px);
  margin-top: 10px;
  padding: 8px;
  font-size: 16px;
}

#wallet-info p {
  margin: 5px 0;
}

#wallet-address {
  word-break: break-all;
  font-weight: bold;
}

#tonkey-balance {
  color: green;
  font-weight: bold;
}

#map-grid-container {
  grid-gap: 3px;
  aspect-ratio: 1;
  background-color: #8b4513;
  border: 2px solid #8b4513;
  border-radius: 8px;
  max-width: 400px;
  max-height: 400px;
  margin: 15px auto;
  padding: 5px;
  display: grid;
  box-shadow: 0 4px 8px #0003, inset 0 0 10px #0000001a;
}

.map-cell {
  aspect-ratio: 1;
  color: #0000;
  background-color: #a28a6f;
  border: 1px solid #80654a;
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  transition: all .3s;
  display: flex;
  position: relative;
  overflow: hidden;
}

.map-cell:after {
  content: "?";
  color: #ffffff4d;
  opacity: .5;
  font-size: 12px;
  position: absolute;
}

.map-cell:hover {
  cursor: pointer;
  transform: scale(1.05);
  box-shadow: 0 0 5px #ffffff4d;
}

.map-cell.discovered {
  color: #3e2723;
  background-color: #a5d6a7;
  border: 1px solid #4caf50;
}

.map-cell.discovered.terrain-grass {
  background-color: #a5d6a7;
}

.map-cell.discovered.terrain-hills {
  background-color: #c5e1a5;
}

.map-cell.discovered.terrain-mountains {
  background-color: #bcaaa4;
}

.map-cell.discovered.terrain-water {
  background-color: #90caf9;
}

.map-cell.discovered.terrain-forest {
  background-color: #66bb6a;
}

.map-cell.discovered.terrain-desert {
  background-color: #ffe082;
}

