}

.map-cell.discovered.special-location {
  animation: 2s ease-in-out infinite pulse;
  box-shadow: 0 0 8px gold;
}

.map-cell.discovered.current-location {
  position: relative;
}

.map-cell.discovered.current-location:before {
  content: "üê¥";
  z-index: 2;
  font-size: 14px;
  position: absolute;
}

@keyframes pulse {
  0% {
    box-shadow: 0 0 5px #ffd70080;
  }

  50% {
    box-shadow: 0 0 15px #ffd700cc;
  }

  100% {
    box-shadow: 0 0 5px #ffd70080;
  }
}

#map-container-wrapper {
  max-width: 400px;
  margin: 0 auto;
  position: relative;
}

#map-legend {
  text-align: left;
  grid-template-columns: repeat(3, 1fr);
  gap: 5px;
  max-width: 400px;
  margin: 15px auto;
  font-size: .85em;
  display: grid;
}

.legend-item {
  align-items: center;
  margin-bottom: 5px;
  display: flex;
}

.legend-color {
  border: 1px solid #0003;
  border-radius: 3px;
  width: 15px;
  height: 15px;
  margin-right: 5px;
}

#map-message {
  background-color: #ffffffb3;
  border-left: 3px solid #e57a44;
  border-radius: 5px;
  margin-top: 10px;
  padding: 8px;
  font-style: italic;
}
